*{
  transition: .5s;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body{
  background: url('https://i.pinimg.com/originals/76/8d/85/768d8569299c97d24532ac413c6c57ac.png');
  background-repeat: no-repeat;
  background-size: cover;
}
#quote-box{
  padding: 1rem;
  width: 500px;
  margin: auto;
  border-radius: 1rem;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(30px);
  background: rgba(0, 0, 0, 0.1);
}
#text{
  height: 350px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: auto;
  margin: 2rem 0;
}
#text img{
  width: 200px;
  height: 350px;
}
#author{
  display: flex;
  justify-content: space-between;
  font-size: 2rem;
  opacity: .8;
  font-weight: 600;
  color: #FFF;
}
#new-quote{
  border: none;
  background: indianred;
  color:#FFF;
  font-size: 1.5rem;
  padding: 1rem 2rem;
  cursor: pointer;
  border-radius: 5px;
}
#tweet-quote{
  display: flex;
  align-items: center;
  background: #FFF;
  border-radius: 5px;
}
#tweet-quote:hover{
  box-shadow:0 0 10px #03a9f4;
}
#tweet-quote img{
  width: 50px;
  padding: .1rem;
}
.top{
  display: grid;
}
.bottom{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0 0 0;
}
.role{
  padding: .5rem 1rem;
  color: #fff;
  font-size: 1.5rem;
}